// Override MediaWiki buttons
.mw-ui-button {
  @extend .btn;
  @extend .btn-secondary;
}

// Link inside button (wiki text doesn't support class of internal and external link)
.btn a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  margin: -0.375rem -0.75rem;
  padding: 0.375rem 0.75rem;
}
.btn-sm a {
  margin: -0.25rem -0.5rem;
  padding: 0.25rem 0.5rem;
}
.btn-lg a {
  margin: -0.5rem -1rem;
  padding: 0.5rem 1rem;
}
